import React, { useCallback, useState } from 'react';
import styles from './SalaryChange.module.css';
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Alert,
  Backdrop,
  Box,
  Button,
  CircularProgress,
  Snackbar,
  Typography
} from '@mui/material';
import { FieldValues, FormProvider, SubmitHandler, useForm } from 'react-hook-form';
import AlertModal from '../../components/AlertModal/AlertModal';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import SalaryValueInput, { SalaryValueInputProps } from './components/SalaryValueInput';
import { NetSalaryResponse } from '../../api/types/salary';

const SalaryChange = () => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string>();
  const [alertOpen, setAlertOpen] = React.useState(false);
  const [alertContent, setAlertContent] = React.useState<
    { title: string; msg: string } | undefined
  >(undefined);
  const [result, setResult] = React.useState<NetSalaryResponse>();

  const setAlert = (msg: string, title?: string) => {
    setAlertContent({ title: title || '', msg: msg });
    setAlertOpen(true);
  };
  const handleCloseAlert = () => {
    setAlertOpen(false);
    setAlertContent(undefined);
  };

  type CommissionInput = {
    otc: number;
    quota: number;
    splitPct?: number;
  };

  type SalaryChangeEmployeeInputResponse = {
    grossSalary?: number;
    agreedChange?: number;
    askedChange?: number;
    commission?: {
      lastYearAchievement?: number;
      lastYear: CommissionInput;
      currentYear: CommissionInput;
    };
  };

  const [employeeInput, setEmployeeInput] = useState<SalaryChangeEmployeeInputResponse>({
    grossSalary: 62500,
    agreedChange: 7500,
    askedChange: 7500,
    commission: {
      lastYearAchievement: 4276533,
      lastYear: { otc: 400000, quota: 4000000 },
      currentYear: { otc: 400000, quota: 5500000, splitPct: 0.15 }
    }
  });

  const [isManager, setIsManager] = useState(false);

  const getFromLocalStorage = (name: string) => {
    const value = localStorage.getItem(name);
    return value ? Number(value) : undefined;
  };
  const CPI = 4.4;
  const defaultValue = useCallback(
    (name: string) => {
      const ls = getFromLocalStorage(name);
      if (ls) return ls;
      const nameArray = name.split('.');
      let result = employeeInput[nameArray[0] as keyof SalaryChangeEmployeeInputResponse];
      if (result) {
        for (let i = 1; i < nameArray.length; i++) {
          result = result[nameArray[i] as keyof typeof result];
        }
      }
      return result as number | undefined;
    },
    [employeeInput]
  );

  const formMethods = useForm({
    defaultValues: {
      grossSalary: defaultValue('grossSalary'),
      agreedChange: defaultValue('agreedChange'),
      askedChange: defaultValue('askedChange')
    }
  });

  const [fieldsInEditMode, setFieldsInEditMode] = useState(false);

  const onSubmit: SubmitHandler<FieldValues> = (formData) => {
    setFieldsInEditMode(false);
    console.log(JSON.stringify({ ...formData }));
  };

  const lightSelectStyle = {
    '& .MuiInputBase-root': {
      color: 'primary.contrastText',
      '.MuiSvgIcon-root': {
        color: 'primary.contrastText'
      }
    },
    '.MuiFormLabel-root': {
      color: 'primary.light',
      '&.Mui-focused': {
        color: 'primary.light'
      }
    }
  };
  const lightFieldStyle = {
    '& .MuiInputBase-root': {
      color: 'primary.contrastText'
    },
    '.MuiFormLabel-root': {
      color: 'primary.light'
    }
  };

  const persistLocalStorage = (name: string): ((value: number) => void) => {
    return (value: number) => localStorage.setItem('grossSalary', String(value));
  };

  const NumberInput = ({ ...props }: SalaryValueInputProps) => {
    return (
      <SalaryValueInput
        isEditMode={fieldsInEditMode}
        notEditable={isManager}
        mt={0}
        onChange={(v) => {
          !!v ? localStorage.setItem(props.name, String(v)) : localStorage.removeItem(props.name);
        }}
        hideSlider
        {...props}
      />
    );
  };

  return (
    <>
      <div className={styles.main}>
        <h1 className={styles.header}>{'Rättelse lön'}</h1>
        <div className={styles.wrapper}>
          <FormProvider {...formMethods}>
            <form onSubmit={formMethods.handleSubmit(onSubmit)}>
              <NumberInput
                name={'grossSalary'}
                label="Nuvarande lön:"
                textFieldSx={lightFieldStyle}
              />
              <NumberInput
                name={'agreedChange'}
                label="Överenskommen justering:"
                textFieldSx={lightFieldStyle}
                showPlus
              />
              <NumberInput
                name={'askedChange'}
                label="Begärd justering:"
                textFieldSx={lightFieldStyle}
                showPlus
              />
              <Accordion sx={{ mt: '12px' }} className={styles.desc}>
                <AccordionSummary
                  expandIcon={<ExpandMoreIcon />}
                  aria-controls="panel1a-content"
                  id="panel1a-header"
                >
                  <Typography>Rörlig lön</Typography>
                </AccordionSummary>
                <AccordionDetails>
                  <Typography>Föregående år:</Typography>
                  <SalaryValueInput
                    label="Budget:"
                    notEditable={isManager}
                    mt={0}
                    name="commission.lastYear.quota"
                    defaultValue={getFromLocalStorage('commission.lastYear.quota')}
                    onChange={(v) => localStorage.setItem('commission.lastYear.quota', String(v))}
                    hideSlider
                  />
                  <SalaryValueInput
                    label="Split:"
                    notEditable={isManager}
                    mt={0}
                    name="commission.lastYear.splitPct"
                    onChange={(v) =>
                      localStorage.setItem('commission.lastYear.splitPct', String(v))
                    }
                    hideSlider
                  />
                </AccordionDetails>
              </Accordion>
              <Button sx={{ mt: '12px' }} type="submit" variant="outlined" fullWidth>
                Beräkna
              </Button>
            </form>
          </FormProvider>
        </div>
        {result && (
          <div className={styles.wrapper}>
            <Typography mt={2}>Utan bil</Typography>
            <Box display="flex" justifyContent={'space-between'}>
              <Typography>Bruttolön:</Typography>
              <Typography>{result.grossSalary}</Typography>
            </Box>
            <Box display="flex" justifyContent={'space-between'}>
              <Typography>Skatt:</Typography>
              <Typography>{result.tax * -1}</Typography>
            </Box>
            <Box display="flex" justifyContent={'space-between'}>
              <Typography sx={{ fontWeight: 'bold' }}>Nettolön:</Typography>
              <Typography sx={{ fontWeight: 'bold' }}>{result.netSalary}</Typography>
            </Box>
            {result.withCar && (
              <>
                <Typography mt={2}>Med bil</Typography>
                <Box display="flex" justifyContent={'space-between'}>
                  <Typography>Bruttolön:</Typography>
                  <Typography>{result.grossSalary - result.withCar.totalGrossDeduction}</Typography>
                </Box>
                <Box display="flex" justifyContent={'space-between'}>
                  <Typography>Skatt:</Typography>
                  <Typography>{result.withCar.tax * -1}</Typography>
                </Box>
                <Box display="flex" justifyContent={'space-between'}>
                  <Typography sx={{ fontWeight: 'bold' }}>Nettolön:</Typography>
                  <Typography sx={{ fontWeight: 'bold' }}>{result.withCar.netSalary}</Typography>
                </Box>
                <Box display="flex" justifyContent={'space-between'}>
                  <Typography>Kostnad bil:</Typography>
                  <Typography>{result.withCar.totalNetCost}</Typography>
                </Box>
              </>
            )}
          </div>
        )}
      </div>
      <AlertModal
        onClose={handleCloseAlert}
        open={alertOpen}
        title={alertContent ? alertContent.title : undefined}
        message={alertContent ? alertContent.msg : undefined}
      />
      <Backdrop sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }} open={loading}>
        <CircularProgress color="inherit" />
      </Backdrop>
      <Snackbar open={!!error} anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}>
        <Alert onClose={() => setError(undefined)} severity="error" sx={{ width: '100%' }}>
          {error}
        </Alert>
      </Snackbar>
    </>
  );
};

export default SalaryChange;
