import React, { FC, useCallback, useEffect, useMemo, useState } from 'react';
import { IconButton, Slider, Stack, StackProps, TextField, Typography } from '@mui/material';
import CheckIcon from '@mui/icons-material/Check';
import EditIcon from '@mui/icons-material/Edit';
import ThumbDownIcon from '@mui/icons-material/ThumbDown';
import ThumbUpIcon from '@mui/icons-material/ThumbUp';
import { StepCalculator } from '../../../utils/Slider';
import { useFormContext, useWatch } from 'react-hook-form';

export interface SalaryValueInputProps extends Omit<StackProps, 'onChange'> {
  name: string;
  label?: string;
  showPlus?: boolean;
  suffix?: string;
  defaultValue?: number;
  onChange?: (newValue: number | string) => void;
  textFieldSx?: {};
  sliderStep?: number;
  sliderMax?: number;
  hideSlider?: boolean;
  notEditable?: boolean;
  isEditMode?: boolean;
}

const SalaryValueInput: FC<SalaryValueInputProps> = ({
  name,
  label,
  showPlus,
  suffix,
  defaultValue,
  onChange,
  textFieldSx,
  sliderStep,
  sliderMax,
  hideSlider,
  notEditable,
  isEditMode = false,
  ...props
}) => {
  const { register, watch, setValue, getValues } = useFormContext();
  const [isEdit, setIsEdit] = useState(false);

  const [baseValue, setBaseValue] = useState(defaultValue || getValues(name));
  const [currentValue, setCurrentValue] = useState(baseValue);
  // const currentValue = watch(name) || baseValue;

  useEffect(() => {
    console.log(isEdit);
    setIsEdit(isEditMode);
    console.log(isEdit);
  }, [isEditMode]);
  // initial load
  useEffect(() => {
    if (isEdit) {
      setBaseValue(currentValue);
    }
  }, [currentValue, baseValue, isEdit]);

  const calcStep = useCallback(StepCalculator, []);

  const step = useMemo<number>(() => {
    return sliderStep || calcStep(Math.abs(baseValue * 0.1), 0.5, 10);
  }, [baseValue, sliderStep, calcStep]);

  const max = useMemo<number>(() => {
    return sliderMax || (baseValue ? baseValue * 2 : 100);
  }, [baseValue, sliderMax]);

  const handleSliderClickChange = (direction: 'up' | 'down') => {
    const inputValue = currentValue || 0;
    const newValue = direction == 'up' ? Number(inputValue) + step : inputValue - step;
    if (newValue < 0) return;
    setValue(name, newValue);
    setCurrentValue(newValue);
    onChange && onChange(newValue);
  };

  return (
    <>
      <Stack
        display="flex"
        mt={2}
        mb={0}
        justifyContent="space-between"
        direction={'row'}
        spacing={0}
        alignItems={'center'}
        {...props}
      >
        {label && (
          <Typography minWidth="8em" align="left">
            {label}
          </Typography>
        )}
        <div
          style={{
            marginLeft: 0,
            display: 'flex',
            flexDirection: 'row',
            flexWrap: 'nowrap',
            alignItems: 'center',
            justifyContent: 'flex-end'
            //height: '50px'
          }}
        >
          {!notEditable && isEdit ? (
            <TextField
              focused={isEdit}
              autoFocus
              size={'small'}
              defaultValue={baseValue}
              type="number"
              label={undefined}
              variant="standard"
              sx={{
                ...textFieldSx,
                marginLeft: '0px',
                'input::-webkit-outer-spin-button, input::-webkit-inner-spin-button': {
                  WebkitAppearance: 'none',
                  margin: 0
                },
                'input[type=number]': {
                  MozAppearance: 'textfield'
                }
              }}
              inputProps={{
                style: { textAlign: 'right' }
              }}
              {...register(name, {
                valueAsNumber: true,
                onChange: (e) => {
                  setCurrentValue(e.target.value);
                  if (onChange) onChange(e.target.value);
                }
              })}
              onBlur={(event) => {
                setIsEdit(false);
              }}
            />
          ) : (
            <Typography>
              {currentValue ? `${showPlus && currentValue > 0 ? '+' : ''}${currentValue}` : '—'}{' '}
            </Typography>
          )}
          <Typography ml={'0.3em'}>{`${suffix ? suffix : 'SEK'}`}</Typography>
          <IconButton
            sx={{ visibility: notEditable ? 'hidden' : 'visible' }}
            onClick={() => setIsEdit(!isEdit)}
          >
            {isEdit ? (
              <CheckIcon fontSize="small" color={'success'} />
            ) : (
              <EditIcon fontSize="small" color={'primary'} />
            )}
          </IconButton>
        </div>
      </Stack>
      {!notEditable && !hideSlider && (
        <Stack mt={0} mb={2} direction="row" alignItems="center" spacing={2}>
          <IconButton onClick={() => handleSliderClickChange('down')}>
            <ThumbDownIcon color="error" />
          </IconButton>
          <Slider
            onChange={(e, newValue) => {
              if (onChange) onChange(newValue as number);
              setValue(name, newValue);
              setCurrentValue(newValue);
            }}
            value={currentValue ? Number(currentValue) : 0}
            aria-label="nominal change"
            min={0}
            max={max}
            step={step}
          ></Slider>
          <IconButton onClick={() => handleSliderClickChange('up')}>
            <ThumbUpIcon color="success" />
          </IconButton>
        </Stack>
      )}
    </>
  );
};

export default SalaryValueInput;
